.App {
    display: flex;
  justify-content: center;
  align-items: center;
  padding: 50px 0;
}
.counter{
  border: 2px solid #000000;
    border-radius: 10px;
  max-width: 400px;
  width: 100%;
  min-height: 300px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin-left: 10px;
    background-color: lightcyan;
}
.buttons{
    border: 2px solid #000000;
    border-radius: 10px;
    max-width: 300px;
    width: 100%;
    padding: 40px 0;
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: moccasin;
}
.error{
  color: red;
  font-size: 30px;
  font-weight: bold;
}
button{
    padding: 10px 20px;
    border: 2px solid #555555;
    font-weight: bold;
}
.display{
    border: 2px solid #000000;
    border-radius: 10px;
    padding: 30px;
    margin-bottom: 20px;
    background-color: moccasin;
}
.errorInput{
    border-color:  #FF0000;
    background-color: rgba(255, 0, 0, 0.3);
}
.settings{
    border: 2px solid #000000;
    border-radius: 10px;
    max-width: 400px;
    width: 100%;
    min-height: 300px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin-right: 10px;
    background-color: lightcyan;
}
.inputSettings{
    border: 2px solid #000000;
    border-radius: 10px;
    padding: 30px;
    margin-bottom: 20px;
    background-color: moccasin;
}
input{
    margin: 10px 0 10px 10px;
    border: 2px solid #555555;
    padding: 5px 0;
    background-color: lightcyan;
    font-size: 12px;
    text-align: center;
}
